.svg-pattern-example{
  width: 100%;
  overflow: hidden;
  min-height: 500px;
  background-image: url(../../img/home-page/bg-4.jpg);
  position: relative;
  &::after{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(0,0,0,.4);
  }
}

.brk-svg-pattern-container{
  position: absolute;
  width: 100%;
  overflow: hidden;
  z-index: 3;

  svg{
    position: absolute;
    width: calc(100% + 2px);
  }

  &_top{
    top: -1px;

    svg{
      top: -5px;
    }
  }
  &_bottom{
    bottom: -1px;

    svg{
      bottom: -5px;
    }
  }
}

